.search-box {
  border-radius: 3px;
  background: #ffffff;
  border: 1px solid #ddd;
  padding: 10px 15px 10px 15px;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}


.search-box:active, .search-box:hover {
  border: 1px solid #aaa;
}

.trailimg {
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  height: 100%;
}

.trailrow {
  height: 20em;
  width: 100%;
}

.card {
  width: 360px;
}

.logo {
  margin: 1em 0 1em 1em;
  z-index:99;
}

.search-btn {
  border: none;
  outline: none !important;
  color: #ddd;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
  position: absolute;
  top: -4pxpx;
  right: -5px;
  background-color: white;
  cursor: default;
}

h2 {
  font-size: 48px;
}

input {
  font-size: 19px;
}

.search-form {
  position: relative;
}

.search-btn:focus, .search-btn:hover {
  color: #aaa;
  outline: none !important;
  box-shadow:none !important;
  -webkit-box-shadow:none !important; 
  border:none !important;
  background-color: white;
}
.search-btn:active {
  outline: none !important;
  border:none !important;
}
#PlacesAutocomplete__root {
  width: 100%;
}

.search-bar {
  outline: none !important;
  width: 100% !important;
  border:none !important;
}

.navbar {
  padding: 0em 1em 0em 1em;
  position:fixed;
  background-color: white;
}

.bb {
  border-bottom: 1px solid #D3D3D3;
}

.tr {
  width: 450px;
  margin: 1em;
}
.sub {
  font-size: 52px;
  line-height: 56px;
  font-weight: 400;
  width: 90%;
}

.cbutton {
  cursor: pointer;
}

.green {
  color: #8ccdb0;
}

.logo:disabled {
  color: #8ccdb0 !important;
  cursor: default !important;
  opacity: 1 !important;
}

.log {
  z-index: 99;
  border: 1px solid transparent;
  border-radius: 0px;
  padding-top: 0px;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.log:hover, .selected {
  border-bottom: 1px solid #8ccdb0;
}

.selected {
  cursor: default;
}


.search-bar:focus {
  outline: none !important;
  box-shadow:none !important;
  -webkit-box-shadow:none !important; 
  border:none !important;
}
.Demo__suggestion-icon {
  margin-right: 8px;
}

.Demo__suggestion-item {
  padding: 4px;
  text-align: left;
}

button, button:hover, button:focus, button:active {
  outline: none !important;
  box-shadow:none !important;
  -webkit-box-shadow:none !important; 
  background-color: transparent;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  height:100%;
}

h1,h2,h3,h4 {
  font-family: "futura-pt";
}

h1 {
  font-size: 56px;
  font-weight: 600;
}

p, div, button {
  font-family: 'lato'; 
}

.trailname {
  font-size: 2em;
  font-weight: 500;
}



